"use client";

import { Card } from "@/components/ui/card";
import { useState, useEffect } from "react";

interface HistoryItem {
  id: number;
  date: string;
  action: string;
  details: string;
}

const dummyData: HistoryItem[] = [
  {
    id: 1,
    date: "2024-01-15",
    action: "Connexion",
    details: "Connexion réussie au compte"
  },
  {
    id: 2,
    date: "2024-01-14",
    action: "Modification du profil",
    details: "Mise à jour des informations personnelles"
  },
  {
    id: 3,
    date: "2024-01-13",
    action: "Consultation",
    details: "Consultation des documents récents"
  }
];

export default function Home() {
  const [history, setHistory] = useState<HistoryItem[]>(dummyData);
  const [loading, setLoading] = useState(false);

  return (
    <div className="min-h-screen bg-gray-50">
      {/* En-tête */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <h1 className="text-3xl font-bold text-gray-900">Mon Historique</h1>
          <p className="mt-2 text-sm text-gray-600">
            Consultez votre historique d'activités récentes
          </p>
        </div>
      </header>

      {/* Contenu principal */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {loading ? (
          <div className="text-center">
            <p className="text-gray-600">Chargement...</p>
          </div>
        ) : (
          <div className="space-y-6">
            {history.map((item) => (
              <Card key={item.id} className="p-6 hover:shadow-lg transition-shadow">
                <div className="flex flex-col space-y-2">
                  <div className="flex justify-between items-start">
                    <h3 className="text-lg font-semibold text-gray-900">
                      {item.action}
                    </h3>
                    <span className="text-sm text-gray-500">
                      {new Date(item.date).toLocaleDateString("fr-FR", {
                        year: "numeric",
                        month: "long",
                        day: "numeric"
                      })}
                    </span>
                  </div>
                  <p className="text-gray-600">{item.details}</p>
                </div>
              </Card>
            ))}
          </div>
        )}

        {history.length === 0 && !loading && (
          <div className="text-center py-12">
            <p className="text-gray-600">Aucun historique disponible</p>
          </div>
        )}
      </main>
    </div>
  );
}
